.liquid:after{
	content:'';
	position:absolute;
	width: 140%;
	height: 143%;
	top:-230%;
	left:50%;
	transform: translate(-50%,75%);
	background: rgba(139, 0 ,0,1);
}

.liquid:before{
border-radius: 45%;
background: rgba(139, 0, 0, 0.9);
animation: animateliq 5s linear infinite;
}
.liquid:after{
	border-radius: 35%;
	background: rgba(139, 0 ,0,0.4);
animation: animateliq 10s linear infinite;
}

@keyframes animateliq{
	0%{
		transform:translate(-50%, 75%) rotate(0deg);
	}
	100%{
		transform:translate(-50%, 75%) rotate(360deg);
	}
}

