  #plants{
	transform-origin: center center; 
	transform-box: fill-box;
	transform: perspective(500px);
	animation: rotateReverse 3s infinite 0s linear alternate; 
  }
  
  @keyframes rotateReverse  {
	0% {
	  transform: scaleX(30px);
	  transform: skewX(20deg);
	}
	50% {
	  transform: scaleX(-10px);
	  transform: skewX(12deg);
	}
	100%{
	  transform: scaleX(30px);
	  transform: skewX(5deg);    
	}
  }
  #Manager{
	transform-origin: top left; 
	transform-box: fill-box;
	transform: perspective(200px);
	animation: zooomMgr 5s infinite 0s linear alternate; 
  }
  @keyframes zooomMgr  {
	0% {
	  transform: skewX(1deg) rotateY(-0deg);
	}
	50% {
	  transform: skewX(5deg) rotateY(-10deg);
	}
	100%{
	  transform: skewX(5deg) rotateY(-40deg);  
	}
  }
  #SplashScr{
	transform-origin: top left; 
	transform-box: fill-box;
	transform: perspective(200px);
	animation: zooomScr 5s infinite 0s linear alternate; 
  }
  @keyframes zooomScr  {
	0% {
	  transform: skewX(1deg) translateX(-3px);
	}
	50% {
	  transform: skewX(-5deg) translateX(-3px);
	}
	100%{
	  transform: skewX(-10deg) translateX(-6px);  
	}
  }
  #Drone{
	transform-origin: top center; 
	transform-box: fill-box;
	transform: perspective(200px);
	animation: zooomDrone 3s infinite 0s linear alternate; 
  }
  
  #developer1{
	transform-origin: top center; 
	transform-box: view-box;
	transform: perspective(200px);
	animation: zooomDeveloper 5s infinite 0s linear alternate; 
  }
  
  #Developer2{
	transform-origin: top center; 
	transform-box: view-box;
	transform: perspective(200px);
	animation: zooomDeveloper 5s infinite 0s linear alternate; 
  }
  #mobileApp{
	transform-origin: top center; 
	transform-box: view-box;
	transform: perspective(200px);
	animation: zooomDrone 5s infinite 0s linear alternate; 
  }
  @keyframes zooomDeveloper  {
	0% {
	  transform: rotateY(2deg) translateX(5px);
	}
	50% {
	  transform: rotateY(15deg) translateX(-45px);
	}
	100%{
	  transform: rotateY(17deg) translateX(-60px);  
	}
  }
  
  @keyframes zooomDrone  {
	0% {
	  transform: scaleX(60px);
	  transform: translateX(30px);
	}
	50% {
	  transform: scaleX(0px);
	  transform: translateX(-12px);
	}
	100%{
	  transform: scaleX(30px);
	  transform: translateX(15px);    
	}
  }
  
  #Drone1{
	transform-origin: top top; 
	transform-box: fill-box;
