	animation: zooomDrone 4s infinite 0s linear alternate; 
  }
  
  
  #flag{
	transform-origin: right bottom; 
	transform-box: fill-box;
	animation: rotateFlag 8s infinite 0s linear alternate; 
  }
  
  
  #Hands{
	transform-origin: top center; 
	transform-box: fill-box;
	animation: rotateHands 8s infinite 0s linear alternate; 
  }
  #blocks{
	transform-origin: top center; 
	transform-box: fill-box;
	animation: blocksanim 2s infinite 0s linear alternate; 
  }
  @keyframes rotateHands  {
	0% {
	  transform: skewY(0deg);
	  transform: rotateX(0deg);
	}
	50% {
	  transform: skewY(0deg);
	  transform: rotateX(-30deg);
	  transform: rotateY(-20deg);
	}
	100%{
	  transform: skewY(0deg);
	  transform: rotateX(0deg);    
	}
  } 
  @keyframes rotateFlag  {
	0% {
	  transform: scaleX(-30px);
	  transform: skewX(20deg);
	}
	50% {
	  transform: scaleX(100px);
	  transform: rotateX(100px);
	  transform: skewX(30deg);
	}
	100%{
	  transform: scaleX(-30px);
	  transform: skewX(5deg);    
	}
  }
  
  @keyframes blocksanim  {
	0% {
	  transform: skewX(-30deg) rotateY(-45deg) scaleY(1);
	}
	50% {
	  transform: skewX(0deg) rotateY(0deg) scaleY(0.5);
	}
	100%{
	  transform: skewX(30deg) rotateY(45deg) scaleY(1);
	}
  } 
